import s from './SoldItems.module.css';
import {useNavigate} from "react-router-dom";
import {useEffect} from "react";

const SoldItems = ({isLoggedIn}) => {
    const navigate = useNavigate();

    useEffect(()=>{
        if (!isLoggedIn){
            navigate('/login')
        }
    },[isLoggedIn])

    return(<></>)
}

export default SoldItems;